import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';

const SlidePreview = ({ code, slideIndex }) => {
  const [renderedContent, setRenderedContent] = useState(null);
  const [error, setError] = useState(null);

  useEffect(() => {
    if (!code) {
      setRenderedContent(null);
      setError(null);
      return;
    }

    try {
      // Transform the code to create a renderable component
      const transformedCode = transformSlideCode(code);
      
      // Create a function from the code
      const slideFunction = new Function(
        'React',
        'motion',
        'useState',
        'useEffect',
        `
        const { useState, useEffect } = React;
        ${transformedCode}
        return Slide;
        `
      );

      // Execute the function to get the component
      const SlideComponent = slideFunction(React, motion, useState, useEffect);
      
      setRenderedContent(<SlideComponent />);
      setError(null);
    } catch (err) {
      console.error('Error rendering slide:', err);
      setError(err.message);
      setRenderedContent(null);
    }
  }, [code, slideIndex]);

  const transformSlideCode = (code) => {
    // Transform JSX-like syntax to React.createElement calls
    let transformed = code;
    
    // Handle self-closing tags
    transformed = transformed.replace(
      /<(\w+)([^>]*?)\/>/g,
      (match, tagName, attributes) => {
        const props = parseAttributes(attributes);
        return `React.createElement('${tagName}', ${props})`;
      }
    );
    
    // Handle opening and closing tags
    transformed = transformed.replace(
      /<(\w+)([^>]*?)>(.*?)<\/\1>/gs,
      (match, tagName, attributes, children) => {
        const props = parseAttributes(attributes);
        const transformedChildren = transformChildren(children);
        return `React.createElement('${tagName}', ${props}, ${transformedChildren})`;
      }
    );
    
    return transformed;
  };

  const parseAttributes = (attributeString) => {
    if (!attributeString.trim()) return 'null';
    
    const attributes = {};
    const attrRegex = /(\w+)=(?:{([^}]+)}|"([^"]+)"|'([^']+)')/g;
    let match;
    
    while ((match = attrRegex.exec(attributeString)) !== null) {
      const [, name, jsValue, doubleQuotedValue, singleQuotedValue] = match;
      
      if (jsValue) {
        // Handle JavaScript expressions
        if (name === 'style') {
          attributes[name] = `{${jsValue}}`;
        } else if (name === 'className') {
          attributes[name] = `\`${jsValue}\``;
        } else {
          attributes[name] = jsValue;
        }
      } else {
        // Handle string values
        const value = doubleQuotedValue || singleQuotedValue;
        attributes[name] = `"${value}"`;
      }
    }
    
    if (Object.keys(attributes).length === 0) return 'null';
    
    const props = Object.entries(attributes)
      .map(([key, value]) => `${key}: ${value}`)
      .join(', ');
    
    return `{ ${props} }`;
  };

  const transformChildren = (children) => {
    if (!children.trim()) return '';
    
    // If children contain JSX, transform them recursively
    if (children.includes('<')) {
      return transformSlideCode(children);
    }
    
    // If it's a JavaScript expression, return as is
    if (children.includes('{') && children.includes('}')) {
      return children;
    }
    
    // Otherwise, treat as string literal
    return `"${children.trim()}"`;
  };

  if (error) {
    return (
      <div className="h-full flex flex-col">
        <div className="bg-gray-800 px-4 py-2 border-b border-gray-700">
          <h3 className="text-sm font-medium text-gray-300">Preview</h3>
        </div>
        <div className="flex-1 flex items-center justify-center">
          <div className="text-center p-8">
            <div className="text-red-400 text-lg mb-4"> Syntax Error</div>
            <div className="text-gray-400 text-sm font-mono bg-gray-800 p-4 rounded">
              {error}
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="h-full flex flex-col">
      <div className="bg-gray-800 px-4 py-2 border-b border-gray-700 flex items-center justify-between">
        <h3 className="text-sm font-medium text-gray-300">Preview</h3>
        <div className="text-xs text-gray-500">Slide {slideIndex + 1}</div>
      </div>
      <div className="flex-1 bg-black relative overflow-hidden">
        <motion.div
          key={slideIndex}
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.3 }}
          className="w-full h-full"
        >
          {renderedContent || (
            <div className="h-full flex items-center justify-center">
              <div className="text-gray-500 text-lg">
                Start coding your slide...
              </div>
            </div>
          )}
        </motion.div>
      </div>
    </div>
  );
};

export default SlidePreview;
